.contactSide{
    position: fixed;
    right:0;
    bottom:0;
    width: 4vw;
    height: fit-content;
}

.contactSide__message{
    color: var(--white-200);
    text-align: center;
    transform: rotate(90deg);
    position: relative;
    top: -50px;
}

.contactSide__logos{
    display: flex;
    flex-direction: column;
}
.contactSide__contactLogo{
    width: 50%;
    height: auto;
    padding: 10px;
    display: flex;
    justify-content: center;
}