header{
    position: fixed;
    color: var(--white-200);
    font-family: Audiowide-Regular;
    display: flex;
    flex-direction: row;
    justify-content: right;
    width: 100vw;
}

.header__link{
    text-decoration: none;
    color: var(--white-200);
}

.header__options{
    padding: 20px;
    font-size: 1.2rem;
    cursor: pointer;
    width: 8vw;
    transition: 0.6s ease-in-out;
}

.header__optionsmore{
    visibility: hidden;
    position: relative;
    top: 10px;
}

.header__knowmore:hover > .header__optionsmore{
    visibility: initial;
}

.header__optionmore{
    padding: 4px;
}

